@import url(https://fonts.googleapis.com/css?family=Nunito:300,400,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Fira+Sans&display=swap);
html, body {
  height: 100vh;
  width: 100vw; }

body {
  background-repeat: no-repeat;
  color: #fff;
  background-position: center;
  font-family: 'Nunito', sans-serif; }

section {
  text-align: center;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.2); }

.App, .main-wrapper, .dash-wrapper {
  height: 100%; }

.top {
  width: 100%;
  display: flex; }

.date, .weather {
  width: 50%; }

.date {
  float: left;
  text-align: left; }

.weather {
  float: right;
  text-align: right; }

.time {
  align-self: center;
  width: 100%; }

.hello-caoimhe {
  font-size: 55px;
  align-self: center;
  width: 100%;
  place-self: flex-start; }
  @media only screen and (max-width: 991px) {
    .hello-caoimhe {
      font-size: 45px; } }

.quote {
  font-size: 14px;
  font-family: 'Fira Sans', sans-serif;
  position: absolute;
  bottom: 5%;
  width: 100%;
  text-align: center; }

.mainInfo {
  padding-top: 13%;
  display: flex;
  width: 65%;
  margin-right: auto;
  margin-left: auto;
  font-size: 2em;
  height: 100%;
  position: relative;
  flex-wrap: wrap; }
  @media only screen and (max-width: 991px) {
    .mainInfo {
      width: 100%; } }
  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    .mainInfo {
      width: 75%; } }
  .mainInfo .dateContainer {
    color: #fff;
    font-size: 50px; }
  .mainInfo .timeContainer {
    font-size: 130px;
    color: #fff; }
    @media only screen and (max-width: 502px) {
      .mainInfo .timeContainer {
        font-size: 70px; } }
    @media only screen and (min-width: 503px) and (max-width: 580px) {
      .mainInfo .timeContainer {
        font-size: 80px; } }
    @media only screen and (min-width: 581px) and (max-width: 991px) {
      .mainInfo .timeContainer {
        font-size: 100px; } }

.dash-wrapper {
  padding: 3em 3em 0 3em; }
  @media only screen and (max-width: 502px) {
    .dash-wrapper {
      padding: 3em 1em 0 1em; } }

.App {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

